import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import store from '@/store'

const siteName = 'Aспирантура СПбГУТ'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: `Личный кабинет | ${siteName}`,
    }
  },
  {
    path: '/massages',
    name: 'Massages',
    component: () => import('../views/Massages'),
    meta: {
      title: `Сообщения | ${siteName}`,
    }
  },
  {
    path: '/progress',
    name: 'Progress',
    component: () => import('../views/Progress'),
    meta: {
      title: `Успеваемость | ${siteName}`,
    }
  },
  {
    path: '/timetable',
    name: 'Timetable',
    component: () => import('../views/Timetable'),
    meta: {
      title: `Расписание | ${siteName}`,
    }
  },
  {
    path: '/news',
    name: 'News',
    component: () => import('../views/News'),
    meta: {
      title: `Новости | ${siteName}`,
    }
  },
  {
    path: '/add-student',
    name: 'AddStudent',
    component: () => import('../views/AddStudent'),
    meta: {
      title: `Добавление аспиранта | ${siteName}`,
      requiresLogin: true
    }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    alias: '/404',
    component: () => import('../views/404'),
    meta: {
      title: `Страница не найдена | ${siteName}`,
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  to.meta.title
      ? document.title = to.meta.title
      : document.title = siteName

  if (to.matched.some(record => record.meta.requiresLogin) && store.state.isAdmin === false) {
    next("/")
  } else {
    next()
  }
})

router.afterEach(() => {
  window.scrollTo({
    top: 0,
    behavior: 'instant'
  })
})

export default router
