<template>
  <div class="calendar-widget">
    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path
          d="M13.3333 1.66666V4.99999M6.66666 1.66666V4.99999M2.5 8.33333H17.5M4.16667 3.33333H15.8333C16.7538 3.33333 17.5 4.07952 17.5 4.99999V16.6667C17.5 17.5871 16.7538 18.3333 15.8333 18.3333H4.16667C3.24619 18.3333 2.5 17.5871 2.5 16.6667V4.99999C2.5 4.07952 3.24619 3.33333 4.16667 3.33333Z"
          stroke="#396ED8" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
    </svg>
    <span class="calendar-widget__date">
      {{ dateString }}
      <br>
      <span>{{ weekNumber }}-я учебная неделя</span>
    </span>
  </div>

</template>

<script>
export default {
  name: "CalendarWidget",

  computed: {
    dateString() {
      const formatter = new Intl.DateTimeFormat('ru', {
        day: 'numeric',
        month: 'long',
        year: 'numeric'
      })
      return formatter.format(Date.now())
    },
    weekNumber() {
      const now = new Date();
      const firstJan = new Date(now.getFullYear(), 0, 1);
      return  Math.ceil(
          (((now.getTime() - firstJan.getTime()) / 86400000) + firstJan.getDay() + 1) / 7
      ) - 7;
    }
  }
}
</script>

